.featured_item {
    position: relative;
}

.featured_item a {
    position: absolute;
    width: 100%;
    bottom: 0;
    text-decoration: none;
    right: 0;
}

.featured_image {
    height: 330px;
    background-size: cover !important;
    background-image: no-repeat !important;
}

.featured_caption {
    color: white;
    top: 0;
    width: 100%;
    padding: 20px;
    font-weight: 300;
    font-size: 28px;
    box-sizing: border-box;
}
.newsList_item {
    border: 1px solid #f2f2f2;
    background: white;
    margin-top: 0;
    padding: 8px 5px 0 5px;
}

.newsList_item h2 {
    font-size: 13px;
    line-height: 21px;
    margin: 5px 0;
    color: #525252;
}

.newsList_item a{
    text-decoration: none;
}


/*
    Transitions
*/

.newsList_wrapper {
    box-sizing: border-box;
    opacity: 0;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: all .5s ease-in;
}

.newsList_wrapper_enter {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.flex_wrapper {
    display: -webkit-flex;
    display: flex;
    border-bottom: solid 1px #d5d5d5;
    background: #fff;
}

.flex_wrapper .left {
    background-size: cover !important;
}

.flex_wrapper .left div {
    width: 90px;
    height: 90px;
    background-size: 40px !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
}

.flex_wrapper .right {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 10px;
}

.flex_wrapper h2 {
    font-size: 12px;
    color: #525252;
}
.blue_btn {
    text-align: center;
    background: #2196f3;
    border-bottom: 1px solid #d7d7d7;
    font-weight: 500;
    color: #eee;
    padding: 9px;
    cursor: pointer;
    font-size: 15px;

    display: block;
    text-decoration: none;
}
.cardInfo {
    font-size: 11px;
}

.cardInfo i {
    margin-right: 5px;
}

.teamName {
    margin-right: 7px;
    background: #d1d1d1;
    color: white;
    padding: 2px 5px;
}

.date {
    color: #2196f3;
}
.videoList_wrapper h3 {
    text-align: center;
    margin: 20px;
    color: #353535;
    font-weight: 300;
    font-size: 21px;
}

.videoListItem_wrapper {
    display: -webkit-flex;
    display: flex;
    border-bottom: 2px solid #d5d5d5;
    background: #fff;
}

.videoListItem_wrapper h2 {
    font-size: 13px;
    color: #525252;
}

.videoListItem_wrapper .left {
    background-size: cover !important;

}

.videoListItem_wrapper .left div {
    width: 90px;
    height: 90px;
    background-size: 40px !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
    background: url('/images/play.png')
}

.videoListItem_wrapper .right {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 10px;
}

.relatedWrapper {
    margin: 0 5px;
}
body {
    margin: 0;
    padding: 0;
    background: #ececec;
    font-family: 'Roboto', sans-serif;
}

a {
    text-decoration: none;
}
.header {
    background: #242424;
    border-bottom: 1px solid #000;
}

.headerOpt {
    display: -webkit-flex;
    display: flex;
}

.logo {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding-top: 8px;
}

.logo img {
    width: 35px;
}
.option {
    font-weight: 300;
    font-size: 12px;
    color: #bababa;
    padding: 10px;
    border-top: 1px solid #404040;
}

.option a {
    color: inherit;
    text-decoration: none;
}

.option span {
    color: white;
    margin-right: 10px;
}
.footer {
    margin-top: 20px;
    background: #242424;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.logo img {
    height: 20px;
    padding: 10px;
}

.right {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    color: #878787;
    font-size: 12px;
}
.articleTeamHeader{
    border: 1px solid #c5c5c5;
    border-bottom: 0px;
    background: white;
    margin: 5px 5px 0 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.articleTeamHeader .left {
    width: 57px;
    height: 50px;
    background-size: 65% !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
}

.articleTeamHeader .right {
