    font-weight: 300;
}

.articlePostData {
    border: 1px solid #c5c5c5;
    border-bottom: 0px;
    background: white;
    margin: 0 5px 0 5px;
    padding: 10px;
    font-size: 13px;
    font-weight: 300;
}

.articlePostData span {
    font-weight: 500;
}
.articleBody {
    background: white;
    margin: 0 5px;
    border: 1px solid #c5c5c5;
    padding: 10px;
}

.articleBody h1 {
    font-size: 25px;
    font-weight: 400;
    color: #4d4d4d;
    margin: 15px 0;
}

.articleImage {
    background-size: cover !important;
    width: 100%;
    height: 200px;
    background-position: center center !important;
    background-repeat: no-repeat !important;
}

.articleText {
    font-weight: 400;
    color: #666666;
    line-height: 23px;
    margin: 15px 0;
}

.videoWrapper {
    background: white;
    margin: 0 5px;
    border: 1px solid #c5c5c5;
}

.videoWrapper h1 {
    font-size: 25px;
    font-weight: 400;
    color: #4d4d4d;
    margin: 15px 0;
    padding: 0 10px;
}

.videoWrapper iframe {
    border: 0;
}

